header {
  height: 100vh;
  padding-top: 5.5rem;
  overflow: hidden;
}

/* -------------------
header container 
------------------- */
.header__container {
  text-align: center;
  position: relative;
  height: 100%;
}

.header__container-subTitle {
  font-size: 1rem;
}
/* -------------------
header Call to Action 
------------------- */
.cta {
  margin-top: 2.5rem;
  display: flex;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  justify-content: center;
}

/* -------------------
header socials
------------------- */
.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 0.7rem;
  gap: 0.7rem;
  position: absolute;
  bottom: 6rem;
}
.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background-color: var(--color-primary);
}

/* -------------------
header image
------------------- */

.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 25rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 11.5rem);
  margin-top: 4rem;
  overflow: hidden;
  padding: 3rem 1.5rem 1.5rem;
  border-radius: 12rem 12rem 0 0;
}

/* -------------------
Scroll Down
------------------- */

.scroll__down {
  position: absolute;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  right: -2.3rem;
  bottom: 8rem;
  font-weight: 300;
  font-size: 0.9rem;
}


/* -------------------  Media Devices [ Medium Devices ] ------------------- */

@media screen and (max-width: 1024px) {
  .header {
      height: 68vh;
  }
}

/* -------------------  Media Devices [ Small Devices ] ------------------- */

@media screen and (max-width: 600px) {
  .header__socials,
  .scroll__down {
    display: none;
}
}

nav {
    background: rgba(0,0,0, 0.2);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.6rem 2rem;
    z-index: 2;
    position: fixed;
    display: flex;
    bottom: 2rem;
    left: 50%;
    grid-gap: 0.9rem;
    gap: 0.9rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
nav a {
    background: transparent;
    padding: 0.9rem ;
    display: flex;
    border-radius: 50%;
    color: var(--color-light);
    font-size: 1.1rem;
}
nav a:hover {
    background-color: rgba(0, 0, 0, 0.5);
}
nav a.active {
    background-color: rgba(0, 0, 0, 0.5);
    color: var(--color-white)
}

nav spam {
    background: transparent;
    padding: 0.5rem ;
    display: flex;
    border-radius: 50%;
    color: var(--color-light);
    font-size: 1rem;
    transition: var(--transition);
}
.nav__weatherText {
    font-size: 0.7;
}

    /* -------------------  Media Devices [ Small Devices ] ------------------- */
  
    @media screen and (max-width: 600px) {
        nav spam{
            display: none;
        }
      }
      
  
.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  grid-gap: 15%;
  gap: 15%;
}

.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}

.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
}
.about__me-image:hover{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
.about__cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1.6rem;
    text-align: center;
    transition: var(--transition);
    padding: 2rem;
}
.about__card:hover {
    background: transparent;
    border: 1px solid var(--color-light);
    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light)
}

 /* =================== MEDIA DEVICES (MEDIUM DEVICES) ===================  */

 @media screen and (max-width: 1024px){
    .about__container {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }
    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p {
        margin: 1rem 0 1.5rem;
    }
  
  }
  /* =================== MEDIA DEVICES (SMALL DEVICES) ===================  */
 
  @media screen and (max-width: 600px){
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about__cards {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
    .about__content {
        text-align: center;
    }
    .about__content p {
        margin: 1.5rem 0;
    }
  }
.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    gap: 1rem;
}
.experience__container > div {
    background: var(--color-bg-variant);
    border-radius: 2rem;
    padding: 2.4rem 5rem;
    transition: var(--transition);
    border: 1px solid transparent;
}
.experience__container > div:hover {
    background: transparent;
    border: 1px solid var(--color-primary-variant);
}

.experience__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}
.container__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.experience__details {
    display: flex;
    grid-gap: 1.3rem;
    gap: 1.3rem;
    padding: 0.7rem 0;
}
.experience__icon {
    margin-top: 5px;
    color: var(--color-primary);
}

/* -------------------  Media Devices [ Medium Devices ] ------------------- */

@media screen and (max-width: 1024px) {
    .experience__container {
        grid-template-columns: 1fr;
    }
    .experience__container > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
    .experience__content {
        padding: 1rem;
    }
  }
  
  /* -------------------  Media Devices [ Small Devices ] ------------------- */
  
  @media screen and (max-width: 600px) {
    .experience__container{
        grid-gap: 1rem;
        gap: 1rem;
    }
    .experience__container > div {
        width: 100%;
        padding: 2rem 1rem;
    }
  }
  
.portfolio__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem; 
}

.portfolio__item {
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.5rem;
    transition: var(--transition);
    border: 1px solid transparent;
}
.portfolio__item:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}
.item__img {
    border-radius: 1.5rem ;
    overflow: hidden;

}

.portfolio__item h3 {
    margin: 1.2rem 0 2rem; 
    font-size: 1rem;
}
.portfolio__item-cta {
    display: flex;
    margin-bottom: 1.2rem;
    grid-gap: 1rem;
    gap: 1rem;
}

.portfolio__item span {
    color: var(--color-light);
    margin: 0 0 1rem;
    display: inline-block;
    padding: 0.2rem 0.4rem;
    background-color: #295a81;
    border-radius: 0.3rem;
    font-size: .7rem;
}
/* -------------------  Media Devices [ Medium Devices ] ------------------- */

@media screen and (max-width: 1024px) {
    .portfolio__container {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }
  }
  
  /* -------------------  Media Devices [ Small Devices ] ------------------- */
  
  @media screen and (max-width: 600px) {
    .portfolio__container {
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem
    } 
  }
  
